<template>
  <div class="markdown">
    标题：
    <input type="text" style="display:block" v-model="title" />
    简介：
    <textarea name id cols="30" rows="10" style="display:block" v-model="introduce"></textarea>
    类型:
    <input type="text" v-model="artClass">
    <mavon-editor
      v-model="markdown"
      ref="md"
      @change="change"
      @imgAdd="$imgAdd"
      style="min-height: 600px"
    />
    <button @click="submit">提交</button>
    <input type="text" v-model="id">
    <button @click="seed">获取</button>
    <button @click="xiugai">修改</button>
  </div>
</template>


<script>
import { mavonEditor } from "mavon-editor";
import "mavon-editor/dist/css/index.css";
import api from '@/api/index.js'
export default {
  components: {
    mavonEditor,
  },
  data() {
    return {
      title: "", //标题
      introduce: "",
      artClass: "",
      markdown: "", //输入的markdown
      html: "", //及时转的html
      id:''//
    };
  },
  methods: {
    //将图片上传到服务器，返回地址替换到md中
    $imgAdd(pos, $file) {
      var formdata = new FormData();
      formdata.append("image", $file);
      api.imgUpload(formdata).then((res) => {
           this.$refs.md.$img2Url(pos, res.data.data);
      });
    },
    //所有操作都会被解析重新渲染
    change(value, render) {
      this.html = render;
    },
    submit() {
        const article = {
            content: this.html,
            markdown: this.markdown
        };
        const artList = {
            title: this.title,
            article_intridyce: this.introduce,
            article_type: this.artClass,
            ArticleId:null

        }
      api.writeArticle(article).then(res=>{
          artList.ArticleId = res.data.id;
          api.upArticleList(artList)
      })
    },
    seed(){
      api.getArticle(this.id).then(res=>{
        this.markdown = res.data.markdown;
        this.id = res.data.id;
      })
    },
    xiugai(){
      const article = {html:this.html,markdown:this.markdown};
      const id = this.id;
      api.upArticle({id,article})
    }
  },
};
</script>

<style lang="scss">
  @import '@/assets/css/markdown/index.scss';
</style>