<template>
    <div class="aside-container">
        <div class="aside-profile">
            <router-link to='/login'>
                <img src='https://ss0.bdstatic.com/70cFvHSh_Q1YnxGkpoWK1HF6hhy/it/u=2196238091,2327782583&fm=26&gp=0.jpg' alt="五月的博客">
            </router-link>
            <span @click="login" class="aside-profile-title">{{author.author_name}}的博客</span>
            <span class="aside-profile-description">{{author.author_talk}}</span>
        </div>

        <button class="btn" v-show="!show" @click="index">首页</button>
    </div>
</template>

<script>
import api from '@/api/index.js'
export default {
    data(){
        return {
            show: this.$store.state.show,
            author:{}
        }
    },
    created(){
        api.getAuthor(1).then(res=>{
            this.author = res.data;
        })
    },
    methods: {
        login(){
            this.$router.push({path:'login'})
        },
        index(){
            this.$router.push({path: 'index'})
        }
    },
}
</script>


<style lang="scss">
    @import '@/assets/css/aside/index.scss';
</style>